"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import Link from "next/link"

interface Ride {
  id: string
  pickup: string
  dropoff: string
  status: 'pending' | 'accepted' | 'in-progress' | 'completed' | 'cancelled'
  driver?: string
  fare: number
  createdAt: string
}

export default function CustomerDashboard() {
  const [currentRide, setCurrentRide] = useState<Ride | null>(null)
  const [recentRides, setRecentRides] = useState<Ride[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    // Simulate API call to fetch user's rides
    const fetchRides = async () => {
      setLoading(true)
      
      // Mock data - replace with actual API calls
      const mockCurrentRide: Ride = {
        id: "ride-123",
        pickup: "Downtown Plaza",
        dropoff: "Airport Terminal 1",
        status: "accepted",
        driver: "John Smith",
        fare: 25.50,
        createdAt: new Date().toISOString()
      }

      const mockRecentRides: Ride[] = [
        {
          id: "ride-122",
          pickup: "Home",
          dropoff: "Shopping Mall",
          status: "completed",
          driver: "Sarah Johnson",
          fare: 15.75,
          createdAt: new Date(Date.now() - 86400000).toISOString()
        },
        {
          id: "ride-121",
          pickup: "Office",
          dropoff: "Restaurant",
          status: "completed",
          driver: "Mike Wilson",
          fare: 12.25,
          createdAt: new Date(Date.now() - 172800000).toISOString()
        }
      ]

      // Simulate network delay
      setTimeout(() => {
        setCurrentRide(mockCurrentRide)
        setRecentRides(mockRecentRides)
        setLoading(false)
      }, 1000)
    }

    fetchRides()
  }, [])

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'pending': return 'bg-yellow-100 text-yellow-800'
      case 'accepted': return 'bg-blue-100 text-blue-800'
      case 'in-progress': return 'bg-green-100 text-green-800'
      case 'completed': return 'bg-gray-100 text-gray-800'
      case 'cancelled': return 'bg-red-100 text-red-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const formatStatus = (status: string) => {
    return status.split('-').map(word => 
      word.charAt(0).toUpperCase() + word.slice(1)
    ).join(' ')
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-black border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-gray-600">Loading your dashboard...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-black mb-2">Customer Dashboard</h1>
          <p className="text-gray-600">Manage your rides and bookings</p>
        </div>

        {/* Quick Actions */}
        <div className="mb-8">
          <Card>
            <CardHeader>
              <CardTitle>Quick Actions</CardTitle>
              <CardDescription>Book a new ride or manage existing ones</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="flex flex-col sm:flex-row gap-4">
                <Link href="/customer/book" className="flex-1">
                  <Button className="w-full bg-black hover:bg-gray-800 text-white h-12">
                    Book New Ride
                  </Button>
                </Link>
                <Link href="/customer/profile" className="flex-1">
                  <Button variant="outline" className="w-full border-black text-black hover:bg-black hover:text-white h-12">
                    View Profile
                  </Button>
                </Link>
              </div>
            </CardContent>
          </Card>
        </div>

        <div className="grid lg:grid-cols-2 gap-8">
          {/* Current Ride */}
          <div>
            <h2 className="text-xl font-semibold mb-4">Current Ride</h2>
            {currentRide ? (
              <Card className="border-2">
                <CardHeader>
                  <div className="flex justify-between items-start">
                    <div>
                      <CardTitle className="text-lg">Ride #{currentRide.id.slice(-6)}</CardTitle>
                      <CardDescription>Driver: {currentRide.driver}</CardDescription>
                    </div>
                    <Badge className={getStatusColor(currentRide.status)}>
                      {formatStatus(currentRide.status)}
                    </Badge>
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="space-y-3">
                    <div>
                      <p className="text-sm text-gray-600">From</p>
                      <p className="font-medium">{currentRide.pickup}</p>
                    </div>
                    <div>
                      <p className="text-sm text-gray-600">To</p>
                      <p className="font-medium">{currentRide.dropoff}</p>
                    </div>
                    <div className="flex justify-between items-center pt-2 border-t">
                      <span className="text-lg font-semibold">${currentRide.fare.toFixed(2)}</span>
                      <Link href={`/customer/track/${currentRide.id}`}>
                        <Button size="sm" className="bg-black hover:bg-gray-800 text-white">
                          Track Ride
                        </Button>
                      </Link>
                    </div>
                  </div>
                </CardContent>
              </Card>
            ) : (
              <Card>
                <CardContent className="text-center py-12">
                  <div className="w-16 h-16 bg-gray-100 rounded-full mx-auto mb-4 flex items-center justify-center">
                    <span className="text-gray-400 text-2xl">?</span>
                  </div>
                  <p className="text-gray-600 mb-4">No active rides</p>
                  <Link href="/customer/book">
                    <Button className="bg-black hover:bg-gray-800 text-white">
                      Book Your First Ride
                    </Button>
                  </Link>
                </CardContent>
              </Card>
            )}
          </div>

          {/* Recent Rides */}
          <div>
            <h2 className="text-xl font-semibold mb-4">Recent Rides</h2>
            <div className="space-y-4">
              {recentRides.length > 0 ? (
                recentRides.map((ride) => (
                  <Card key={ride.id}>
                    <CardContent className="pt-6">
                      <div className="flex justify-between items-start mb-3">
                        <div className="flex-1">
                          <p className="font-medium text-sm">#{ride.id.slice(-6)}</p>
                          <p className="text-sm text-gray-600">{ride.driver}</p>
                        </div>
                        <Badge className={getStatusColor(ride.status)}>
                          {formatStatus(ride.status)}
                        </Badge>
                      </div>
                      <div className="space-y-1 text-sm">
                        <p><span className="text-gray-600">From:</span> {ride.pickup}</p>
                        <p><span className="text-gray-600">To:</span> {ride.dropoff}</p>
                      </div>
                      <div className="flex justify-between items-center mt-3 pt-3 border-t">
                        <span className="font-semibold">${ride.fare.toFixed(2)}</span>
                        <span className="text-sm text-gray-500">
                          {new Date(ride.createdAt).toLocaleDateString()}
                        </span>
                      </div>
                    </CardContent>
                  </Card>
                ))
              ) : (
                <Card>
                  <CardContent className="text-center py-8">
                    <p className="text-gray-600">No recent rides</p>
                  </CardContent>
                </Card>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
